/* eslint-disable import/no-cycle */
import { BrowserWindow } from 'electron';
import { TestRunner } from '../../shared/Types/Tests.type';
import { DataCode, OpCode, WriteToCMod } from '../CMod/CModManager';
import {
  AutoStepStateChanger,
  CheckCorridors,
  CheckSimHiLo,
  HeightSimulation,
  ReliabilityDiscreteTest,
  RequestUserAttention,
  RequestUserInput,
  TestStepSelectUpDwn,
  YassurHeightSimulationTest,
  YassurMagaDiscreteTest,
  YassurTestSiniUpDn,
  delay,
} from '../utils/TestsHelpers';
import { SimStatus } from '../../shared/Types/LogTypes';
import TesterManager from './TetserManage';

// eslint-disable-next-line import/prefer-default-export
export function createTestsForYassur(mainWindow: BrowserWindow): TestRunner[] {
  return [
    YassurHeightSimulationTest(
      250,
      DataCode.RS250Feet,
      OpCode.GOVA_YARHONIT,
      SimStatus.On,
      SimStatus.On,
      'Warning',
      mainWindow
    ),
    YassurHeightSimulationTest(
      300,
      DataCode.RS300Feet,
      OpCode.GOVA_YARHONIT,
      SimStatus.On,
      SimStatus.Off,
      'Caution',
      mainWindow
    ),
    YassurHeightSimulationTest(
      350,
      DataCode.RS350Feet,
      OpCode.GOVA_YARHONIT,
      SimStatus.On,
      SimStatus.Off,
      'Caution',
      mainWindow
    ),
    YassurHeightSimulationTest(
      400,
      DataCode.RS400Feet,
      OpCode.GOVA_YARHONIT,
      SimStatus.Off,
      SimStatus.Off,
      'Silence',
      mainWindow
    ),
    YassurTestSiniUpDn(
      'Up',
      OpCode.SINI_UP,
      1000,
      800,
      SimStatus.On,
      SimStatus.On,
      mainWindow,
      'Warning'
    ),
    YassurTestSiniUpDn(
      'Down',
      OpCode.SINI_DN,
      350,
      300,
      SimStatus.Off,
      SimStatus.Off,
      mainWindow,
      'Silence'
    ),
    YassurHeightSimulationTest(
      0,
      DataCode.RS0Feet,
      OpCode.GOVA_YARHONIT,
      SimStatus.On,
      SimStatus.On,
      'Warning',
      mainWindow
    ),
    YassurHeightSimulationTest(
      400,
      DataCode.RS400Feet,
      OpCode.GOVA_YARHONIT,
      SimStatus.Off,
      SimStatus.Off,
      'Silence',
      mainWindow
    ),
    ReliabilityDiscreteTest(
      'חיפוש',
      'Caution',
      DataCode.ReliabilitySearch,
      mainWindow
    ),
    YassurHeightSimulationTest(
      300,
      DataCode.RS300Feet,
      OpCode.GOVA_YARHONIT,
      SimStatus.Off,
      SimStatus.Off,
      'Silence',
      mainWindow
    ),
    ReliabilityDiscreteTest(
      'חיפוש',
      'Caution',
      DataCode.ReliabilitySearch,
      mainWindow
    ),
    TestStepSelectUpDwn('מעלה', 1000, 800, mainWindow, 'Warning'),
    TestStepSelectUpDwn('מטה', 350, 300, mainWindow, 'Caution'),
    {
      name: `מתג Step Select מטה`,
      testFunc: async () => {
        const attentionMsg = 'הזז את מתג ה-Step Select מטה';

        WriteToCMod(OpCode.GOVA_YARHONIT, DataCode.RS0Feet);
        await RequestUserAttention(mainWindow, attentionMsg);

        try {
          CheckCorridors(100, 80);
          CheckSimHiLo(SimStatus.On, SimStatus.On);

          RequestUserInput(
            mainWindow,
            '',
            `האם שמעת Warning`,
            'לא נשמע סאונד מתאים'
          );

          return {
            testName: `מתג Step Select מטה`,
            result: true,
            message: '',
          };
        } catch (error) {
          return {
            testName: `מתג Step Select מטה`,
            result: false,
            message: (error as Error).message,
          };
        }
      },
    },
    AutoStepStateChanger('On', mainWindow),
    {
      name: 'דמה גובה 200',
      testFunc: async () => {
        WriteToCMod(OpCode.GOVA_YARHONIT, DataCode.RS200Feet);
        await delay(63000);
        try {
          CheckCorridors(100, 80);
          CheckSimHiLo(0, 0);
          if (TesterManager.getInstance().EcarosLog?.analogHeight !== 200)
            throw new Error('גובה לא תואם לדימוי');
          RequestUserInput(
            mainWindow,
            '',
            `האם שמעת Silence`,
            'לא נשמע סאונד מתאים'
          );
          return {
            testName: 'דמה גובה 200',
            result: true,
            message: '',
          };
        } catch (error) {
          return {
            testName: 'דמה גובה 200',
            result: false,
            message: (error as Error).message,
          };
        }
      },
    },
    {
      name: `מתג AutoStep ל-Off`,
      testFunc: async () => {
        const attentionMsg = `מתג AutoStep ל-Off`;
        await RequestUserAttention(mainWindow, attentionMsg);

        try {
          const result = RequestUserInput(
            mainWindow,
            `מתג AutoStep ל-Off`,
            'האם סמנים משתנים בהתאם לשינויים?',
            'תקלה בסמני חיווי מג"א'
          );
          return result;
        } catch (error) {
          return {
            testName: `מתג AutoStep ל-Off`,
            result: false,
            message: (error as Error).message,
          };
        }
      },
    },
    AutoStepStateChanger('On', mainWindow),
    {
      name: `מעבר למסדרון 50/40`,
      testFunc: async () => {
        const attentionMsg = 'הזז את מתג ה-Step Select מטה עד למסדרון 50/40';
        await RequestUserAttention(mainWindow, attentionMsg);

        return {
          testName: `מעבר למסדרון 50/40`,
          result: true,
          message: '',
        };
      },
    },
    {
      name: `דימוי גובה 900`,
      testFunc: async () => {
        const testName = `דימוי גובה 900`;
        const result = await HeightSimulation(
          900,
          DataCode.RS900Feet,
          OpCode.GOVA_YARHONIT,
          SimStatus.Off,
          SimStatus.Off
        );

        try {
          RequestUserInput(
            mainWindow,
            testName,
            'האם לא היה שום חיווי קולי?',
            'לא נשמע סאונד'
          );
          RequestUserAttention(mainWindow, 'נחכה 60 שניות לראות קפיצת מסדרון');
          delay(60000);
          CheckCorridors(350, 300);

          return {
            testName,
            ...result,
          };
        } catch (error) {
          return {
            testName,
            result: false,
            message: (error as Error).message,
          };
        }
      },
    },
    {
      name: `דימוי גובה 1300`,
      testFunc: async () => {
        const testName = `דימוי גובה 1300`;
        const result = await HeightSimulation(
          900,
          DataCode.RS900Feet,
          OpCode.GOVA_YARHONIT,
          SimStatus.Off,
          SimStatus.Off
        );

        try {
          if (TesterManager.getInstance().EcarosLog?.analogHeight !== 1300)
            throw Error('גובה אנלוגי לא נכון');

          return {
            testName,
            ...result,
          };
        } catch (error) {
          return {
            testName,
            result: false,
            message: (error as Error).message,
          };
        }
      },
    },
    {
      name: `דימוי גובה 1300`,
      testFunc: async () => {
        const testName = `דימוי גובה 1300`;
        const result = await HeightSimulation(
          900,
          DataCode.RS900Feet,
          OpCode.GOVA_YARHONIT,
          SimStatus.Off,
          SimStatus.Off
        );

        try {
          if (TesterManager.getInstance().EcarosLog?.analogHeight !== 1300)
            throw Error('גובה אנלוגי לא נכון');

          return {
            testName,
            ...result,
          };
        } catch (error) {
          return {
            testName,
            result: false,
            message: (error as Error).message,
          };
        }
      },
    },
    {
      name: `דימוי גובה 1000 אנלוגי`,
      testFunc: async () => {
        const testName = `דימוי גובה 1000 אנלוגי`;
        const result = await HeightSimulation(
          900,
          DataCode.RS900Feet,
          OpCode.GOVA_YARHONIT,
          SimStatus.Off,
          SimStatus.Off
        );

        try {
          if (TesterManager.getInstance().EcarosLog?.analogHeight !== 1300)
            throw Error('גובה אנלוגי לא נכון');

          return {
            testName,
            ...result,
          };
        } catch (error) {
          return {
            testName,
            result: false,
            message: (error as Error).message,
          };
        }
      },
    },
    {
      name: 'הפסק שידור גובה מירחונית',
      testFunc: async () => {
        const testName = 'הפסק שידור גובה מירחונית';
        WriteToCMod(OpCode.STREAM_GOVA, DataCode.IdleGova);

        try {
          const result = RequestUserInput(
            mainWindow,
            testName,
            'האם הושמע Warning והוצג Fail ו0001',
            'לא נשמע סאונד מתאים'
          );

          return result;
        } catch (error) {
          return {
            testName,
            result: false,
            message: (error as Error).message,
          };
        }
      },
    },
    {
      name: 'הדלקת ירחונית וכיבוי 28v',
      testFunc: async () => {
        const testName = 'הדלקת ירחונית וכיבוי 28v';
        WriteToCMod(OpCode.STREAM_GOVA, DataCode.StreamGovaOnRs);
        WriteToCMod(OpCode.UUT_Power, DataCode.DiscreteOff);

        return {
          testName,
          result: false,
          message: '',
        };
      },
    },
    YassurMagaDiscreteTest(
      OpCode.HI_MAGA,
      DataCode.DiscreteOn,
      'דולק',
      'MAGA-HI',
      SimStatus.On,
      undefined
    ),
    YassurMagaDiscreteTest(
      OpCode.HI_MAGA,
      DataCode.DiscreteOff,
      'כבוי',
      'MAGA-HI',
      SimStatus.Off,
      undefined
    ),
    YassurMagaDiscreteTest(
      OpCode.LO_MAGA,
      DataCode.DiscreteOn,
      'דולק',
      'MAGA-LO',
      undefined,
      SimStatus.On
    ),
    YassurMagaDiscreteTest(
      OpCode.LO_MAGA,
      DataCode.DiscreteOff,
      'כבוי',
      'MAGA-LO',
      undefined,
      SimStatus.Off
    ),
    {
      name: 'בורר עוצמת התאורה ההיקפית',
      testFunc: async () => {
        const attentionMsg = 'שנה את בורר עוצמת התאורה ההיקפית';
        const testName = 'בורר עוצמת התאורה ההיקפית';
        const question = 'האם עוצמת התאורה ההיקפית השתנה?';
        const errMsg = 'בורר עוצמת תאורה היקפית אינו מגיב';

        await RequestUserAttention(mainWindow, attentionMsg);
        try {
          const userResponse = await RequestUserInput(
            mainWindow,
            testName,
            question,
            errMsg
          );
          return userResponse;
        } catch (error) {
          return {
            testName,
            result: false,
            message: (error as Error).message,
          };
        }
      },
    },
    {
      name: 'בורר עוצמת תאורת המסכים',
      testFunc: async () => {
        const attentionMsg = 'שנה את בורר עוצמת תאורת המסכים';
        const testName = 'בורר עוצמת תאורת המסכים';
        const question = 'האם עוצמת תאורת המסכים השתנה?';
        const errMsg = 'בורר עוצמת תאורת המסכים אינו מגיב';

        await RequestUserAttention(mainWindow, attentionMsg);
        try {
          const userResponse = await RequestUserInput(
            mainWindow,
            testName,
            question,
            errMsg
          );
          return userResponse;
        } catch (error) {
          return {
            testName: 'בורר עוצמת תאורת המסכים',
            result: false,
            message: (error as Error).message,
          };
        }
      },
    },
  ];
}
