/* eslint-disable promise/always-return */
/* eslint-disable promise/catch-or-return */
/* eslint-disable no-undef */
import { BrowserWindow } from 'electron';
import { TestRunner } from '../../shared/Types/Tests.type';
// eslint-disable-next-line import/no-cycle
import {
  AutoStepStateChanger,
  CheckCorridors,
  CheckSimHiLo,
  FireDiscreteTest,
  OferHeightSimulationTest,
  RequestUserAttention,
  RequestUserInput,
  TestSiniUpDn,
  TestStepSelectUpDwn,
  delay,
} from '../utils/TestsHelpers';
import TesterManager from './TetserManage';
import { DataCode, OpCode, WriteToCMod } from '../CMod/CModManager';

// eslint-disable-next-line import/prefer-default-export
export function createTestsForOfer(mainWindow: BrowserWindow): TestRunner[] {
  return [
    {
      name: 'מסדרונות התחלתיים',
      testFunc: async () => {
        try {
          const result = CheckCorridors(800, 600);
          return {
            testName: 'מסדרונות התחלתיים',
            ...result,
          };
        } catch (error) {
          return {
            testName: 'מסדרונות התחלתיים',
            result: false,
            message: (error as Error).message,
          };
        }
      },
    },
    OferHeightSimulationTest(
      599,
      DataCode.Arinc598Feet,
      OpCode.GOVA_ARINC,
      1,
      1
    ),
    OferHeightSimulationTest(
      600,
      DataCode.Arinc600Feet,
      OpCode.GOVA_ARINC,
      1,
      0
    ),
    OferHeightSimulationTest(
      800,
      DataCode.Arinc800Feet,
      OpCode.GOVA_ARINC,
      1,
      0
    ),
    OferHeightSimulationTest(
      801,
      DataCode.Arinc802Feet,
      OpCode.GOVA_ARINC,
      0,
      0
    ),
    TestSiniUpDn('UP', OpCode.SINI_UP, 1000, 800, 1, 1),
    {
      name: 'לחיצה על IN',
      testFunc: async () => {
        const testName = 'לחיצה על IN';
        const question = 'האם צפצפות הושתקו';
        const errMsg = 'צפצפות לא הושתקו';

        WriteToCMod(OpCode.SINI_IN, DataCode.DiscreteOn);
        WriteToCMod(OpCode.SINI_IN, DataCode.DiscreteOff);

        try {
          const userResponse = await RequestUserInput(
            mainWindow,
            testName,
            question,
            errMsg
          );
          return userResponse;
        } catch (error) {
          return {
            testName,
            result: false,
            message: (error as Error).message,
          };
        }
      },
    },
    {
      name: 'חזרת שמעים',
      testFunc: async () => {
        await delay(2000);
        const testName = 'חזרת שמעים';
        const question = 'האם השמעים חזרו?';
        const errMsg = 'שמעים לא חזרו';

        try {
          const userResponse = await RequestUserInput(
            mainWindow,
            testName,
            question,
            errMsg
          );
          return userResponse;
        } catch (err) {
          return {
            testName: 'חזרת שמעים',
            result: false,
            message: (err as Error).message,
          };
        }
      },
    },
    TestSiniUpDn('DOWN', OpCode.SINI_DN, 800, 600, 0, 0),
    OferHeightSimulationTest(0, DataCode.Arinc0Feet, OpCode.GOVA_ARINC, 1, 1),
    {
      name: 'מצב Test',
      testFunc: async () => {
        const attentionMsg = 'לחץ על בוררי המסדרונות למשך 5 שניות';
        const testName = 'מצב Test';
        const question = 'האם הוצגו והושמעו החיווים הנדרשים?';
        const errMsg = 'מצב Test נכשל';

        await RequestUserAttention(mainWindow, attentionMsg);
        try {
          const userResponse = await RequestUserInput(
            mainWindow,
            testName,
            question,
            errMsg
          );
          return userResponse;
        } catch (error) {
          return {
            testName,
            result: false,
            message: (error as Error).message,
          };
        }
      },
    },
    TestStepSelectUpDwn('מעלה', 1000, 800, mainWindow),
    TestStepSelectUpDwn('מטה', 800, 600, mainWindow),
    {
      name: 'מתג Step Select מטה 4 פעמים',
      testFunc: async () => {
        WriteToCMod(OpCode.GOVA_ARINC, DataCode.Arinc0Feet);

        const attentionMsg = 'הזז את מתג ה-Step Select 4 פעמים מטה';

        await RequestUserAttention(mainWindow, attentionMsg);
        try {
          CheckCorridors(22, 15);
          CheckSimHiLo(1, 1);
          if (TesterManager.getInstance().EcarosLog?.analogHeight !== 0)
            throw new Error('גובה לא תואם לדימוי');

          return {
            testName: 'מתג Step Select מטה 4 פעמים',
            result: true,
            message: '',
          };
        } catch (error) {
          return {
            testName: 'מתג Step Select מטה 4 פעמים',
            result: false,
            message: (error as Error).message,
          };
        }
      },
    },
    {
      name: 'בוררי כיול צפצפות',
      testFunc: async () => {
        WriteToCMod(OpCode.GOVA_ARINC, DataCode.Arinc0Feet);

        const attentionMsg =
          'סובב את בורר כיול הצפצפות העליון עשר יוצג 10\nסובב את בורר כיול הצפצפות התחתון עד אשר יוצג 6\nהמתן 3 שניות לקביעת המסדרון החדש';

        await RequestUserAttention(mainWindow, attentionMsg);
        try {
          CheckCorridors(10, 6);
          CheckSimHiLo(1, 1);

          return {
            testName: 'בוררי כיול צפצפות',
            result: true,
            message: '',
          };
        } catch (error) {
          return {
            testName: 'בוררי כיול צפצפות',
            result: false,
            message: (error as Error).message,
          };
        }
      },
    },
    AutoStepStateChanger('ON', mainWindow),
    {
      name: 'דמה גובה 51',
      testFunc: async () => {
        WriteToCMod(OpCode.GOVA_ARINC, DataCode.Arinc51Feet);
        await delay(3000);
        try {
          CheckCorridors(50, 40);
          CheckSimHiLo(0, 0);
          if (TesterManager.getInstance().EcarosLog?.analogHeight !== 51)
            throw new Error('גובה לא תואם לדימוי');
          return {
            testName: 'דמה גובה 51',
            result: true,
            message: '',
          };
        } catch (error) {
          return {
            testName: 'דמה גובה 51',
            result: false,
            message: (error as Error).message,
          };
        }
      },
    },
    {
      name: 'דמה גובה 350',
      testFunc: async () => {
        WriteToCMod(OpCode.GOVA_ARINC, DataCode.Arinc350Feet);
        await delay(3000);
        try {
          CheckCorridors(100, 80);
          CheckSimHiLo(0, 0);
          if (TesterManager.getInstance().EcarosLog?.analogHeight !== 350)
            throw new Error('גובה לא תואם לדימוי');

          return {
            testName: 'דמה גובה 350',
            result: true,
            message: '',
          };
        } catch (error) {
          return {
            testName: 'דמה גובה 350',
            result: false,
            message: (error as Error).message,
          };
        }
      },
    },
    AutoStepStateChanger('OFF', mainWindow),
    {
      name: 'לחיצה על בורר תצוגה עליון',
      testFunc: async () => {
        const attentionMsg = 'לחץ על בורר תצוגה עליון';
        const testName = 'לחיצה על בורר תצוגה עליון';
        const question =
          'האם ספרת האלפים במסך העליון הבהבה למשך 3 שניות ואח"כ המסדרון נקבע?';
        const errorMsg = 'בורר עליון אינו מגיב';

        await RequestUserAttention(mainWindow, attentionMsg);
        try {
          const userResponse = await RequestUserInput(
            mainWindow,
            testName,
            question,
            errorMsg
          );
          return userResponse;
        } catch (error) {
          return {
            testName,
            result: false,
            message: (error as Error).message,
          };
        }
      },
    },
    {
      name: 'שינוי ספרת אלפים ל-1',
      testFunc: async () => {
        const attentionMsg =
          'לחץ על בורר תצוגה עליון והזז את בורר ספרת האלפים ל-1';
        const testName = 'שינוי ספרת אלפים ל-1';
        const question =
          'האם ספרת האלפים במסך העליון הבהבה למשך 3 שניות ואח"כ המסדרון העליון נקבע ל-1000?';
        const errorMsg = 'בורר עליון אינו מגיב';

        await RequestUserAttention(mainWindow, attentionMsg);
        try {
          const userResponse = await RequestUserInput(
            mainWindow,
            testName,
            question,
            errorMsg
          );
          return userResponse;
        } catch (error) {
          return {
            testName,
            result: false,
            message: (error as Error).message,
          };
        }
      },
    },
    {
      name: 'לחיצה על בורר תצוגה תחתון',
      testFunc: async () => {
        const attentionMsg = 'לחץ על בורר תצוגה תחתון';
        const testName = 'לחיצה על בורר תצוגה תחתון';
        const question =
          'האם ספרת האלפים במסך התחתון הבהבה למשך 3 שניות ואח"כ המסדרון נקבע?';
        const errorMsg = 'בורר תחתון אינו מגיב';

        await RequestUserAttention(mainWindow, attentionMsg);
        try {
          const userResponse = await RequestUserInput(
            mainWindow,
            testName,
            question,
            errorMsg
          );
          return userResponse;
        } catch (error) {
          return {
            testName,
            result: false,
            message: (error as Error).message,
          };
        }
      },
    },
    {
      name: 'שינוי ספרת המאות ל-6',
      testFunc: async () => {
        const attentionMsg =
          'לחץ פעמיים על בורר תצוגה תחתון והזז את בורר ספרת המאות ל-6';
        const testName = 'שינוי ספרת המאות ל-6';
        const question =
          'האם ספרת המאות במסך התחתון הבהבה למשך 3 שניות ואח"כ המסדרון התחתון נקבע ל-680?';
        const errorMsg = 'בורר תחתון אינו מגיב';

        await RequestUserAttention(mainWindow, attentionMsg);
        try {
          const userResponse = await RequestUserInput(
            mainWindow,
            testName,
            question,
            errorMsg
          );
          return userResponse;
        } catch (error) {
          return {
            testName,
            result: false,
            message: (error as Error).message,
          };
        }
      },
    },
    {
      name: 'בדיקת השפעת בוררים',
      testFunc: async () => {
        try {
          return {
            testName: 'בדיקת השפעת בוררים',
            ...CheckCorridors(1000, 680),
          };
        } catch (error) {
          return {
            testName: 'בדיקת השפעת בוררים',
            result: false,
            message: (error as Error).message,
          };
        }
      },
    },
    OferHeightSimulationTest(
      500,
      DataCode.Arinc500Feet,
      OpCode.GOVA_ARINC,
      1,
      1
    ),
    OferHeightSimulationTest(
      680,
      DataCode.Arinc680Feet,
      OpCode.GOVA_ARINC,
      1,
      0
    ),
    OferHeightSimulationTest(
      1000,
      DataCode.Arinc1000Feet,
      OpCode.GOVA_ARINC,
      1,
      1
    ),
    OferHeightSimulationTest(
      1100,
      DataCode.Arinc1100Feet,
      OpCode.GOVA_ARINC,
      0,
      0
    ),
    FireDiscreteTest('Cannon', DataCode.FireCannon, mainWindow),
    FireDiscreteTest('Missle', DataCode.FireMissle, mainWindow),
    {
      name: 'בורר עוצמת התאורה ההיקפית',
      testFunc: async () => {
        const attentionMsg = 'שנה את בורר עוצמת התאורה ההיקפית';
        const testName = 'בורר עוצמת התאורה ההיקפית';
        const question = 'האם עוצמת התאורה ההיקפית השתנה?';
        const errMsg = 'בורר עוצמת תאורה היקפית אינו מגיב';

        await RequestUserAttention(mainWindow, attentionMsg);
        try {
          const userResponse = await RequestUserInput(
            mainWindow,
            testName,
            question,
            errMsg
          );
          return userResponse;
        } catch (error) {
          return {
            testName,
            result: false,
            message: (error as Error).message,
          };
        }
      },
    },
    {
      name: 'בורר עוצמת תאורת המסכים',
      testFunc: async () => {
        const attentionMsg = 'שנה את בורר עוצמת תאורת המסכים';
        const testName = 'בורר עוצמת תאורת המסכים';
        const question = 'האם עוצמת תאורת המסכים השתנה?';
        const errMsg = 'בורר עוצמת תאורת המסכים אינו מגיב';

        await RequestUserAttention(mainWindow, attentionMsg);
        try {
          const userResponse = await RequestUserInput(
            mainWindow,
            testName,
            question,
            errMsg
          );
          return userResponse;
        } catch (error) {
          return {
            testName: 'בורר עוצמת תאורת המסכים',
            result: false,
            message: (error as Error).message,
          };
        }
      },
    },
  ];
}
